<template>
    <div class="homeBody">
        <div class="wrapper" ref="wrapper">
            <div class="content">
                <Swiper :wrapImgs = "home.wrapImgs"></Swiper>
                <div class="classifyTen">
                    <CirClassify v-for="(v,i) of home.classify" :key="'classify'+i" :classify="v"></CirClassify>
                </div>
                <div class="promotion">
                    <span>————— ◆&nbsp;&nbsp;超实惠-促销直达&nbsp;&nbsp;◆ —————</span>
                    <div class="promotionInfo">
                        <div class="promotionEach" v-for="(v,i) of home.bebefit" :key="'bebefit'+i">
                            <div class="promotionDesc">
                                <span>{{ v.title }}</span>
                                <span>{{ v.viceTitle.replace(/^{/,"").replace(/}$/,"") }}</span>
                            </div>
                            <div class="promotionImg">
                                <img :src="v.image">
                            </div>
                        </div>
                    </div>
                </div> 
                <div class="hotClassify">
                    <span>————— ◆&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;热门分类&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;◆ —————</span>
                    <div class="hotEach">
                        <CirClassify v-for="(v,i) of home.hotType" :key="'hotType'+i" :classify="v"></CirClassify>
                    </div>
                </div> 
                <div class="guessYouLike">
                    <span>————— ◆&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;猜你喜欢&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;◆ —————</span>
                    <div class="guessLike">
                        <GoodsInfo v-for="(v,i) of home.likes" :key="'likes'+i" :likes="v"></GoodsInfo>
                    </div>
                </div> 
            </div>
        </div>
    </div>
</template>
<script>
    import BScroll from 'better-scroll';

    import {mapState,mapMutations,mapActions,mapGetters} from "vuex";

    export  default {
        name:"HomePageBody",
        computed:{
            ...mapState(['home'])
        },
        methods:{
            loadRefs(){
                 this.$nextTick(()=>{
                    this.scroll = new BScroll(this.$refs.wrapper,{
                        click:true
                    })
                })
            },
            ...mapActions(["loadHomeData"])
        },
        mounted(){
            this.loadRefs();
            this.loadHomeData();
        }
    }
</script>
<style lang="less" scoped>
    .homeBody{
        position: absolute;
        top:110px;/*px*/
        left:0;
        right:0;
        bottom:103px;/*px*/
        .wrapper{
            height:100%;
        }
        .classifyTen{
            width:100%;
            height:350px;/*px*/
            display:flex;
            flex-wrap: wrap;
            box-sizing: border-box;
            margin-top:50px;/*px*/
        }
        .promotion{
            margin-top:40px;/*px*/
            text-align: center;
            &>span{
                font-size:28px;/*px*/
            }
            .promotionInfo{
                margin-top:15px;/*px*/
                width:100%;
                height:600px;/*px*/
                .promotionEach{
                    outline:solid #ccc thin;
                    font-size:22px;/*px*/
                    & span{
                        display:block;
                    }
                    & span:first-of-type{
                        margin-top:26px;/*px*/
                    }
                    & span:last-of-type{
                        margin-top:10px;/*px*/
                        color:#F56181;
                        font-weight: 200;
                    }
                }
                .promotionEach:nth-of-type(1){
                    width:50%;
                    height:300px;/*px*/
                    float:left;
                    .promotionImg{
                        width:60%;
                        height:180px;/*px*/
                        margin:0 auto;
                        & img{
                            width:100%;
                            height:100%;
                        }
                    }
                }
                .promotionEach:nth-of-type(2), .promotionEach:nth-of-type(3){
                    width:50%;
                    height:150px;/*px*/
                    float:right;
                    text-align: left;
                    .promotionDesc{
                        width:50%;
                        height:150px;/*px*/
                        margin:10px 10px;/*px*/
                        float:left;

                    }
                    .promotionImg{
                        width:43%;
                        height:150px;/*px*/
                        margin:0 auto;
                        float:right;
                        & img{
                            width:100%;
                            height:100%;
                        }
                    }
                }
                .promotionEach:nth-of-type(4), .promotionEach:nth-of-type(5),.promotionEach:nth-of-type(6){
                    width:100%/3;
                    height:300px;/*px*/
                    float:left;
                    .promotionImg{
                        width:70%;
                        height:180px;/*px*/
                        margin:15px auto 0;/*px*/
                        & img{
                            width:100%;
                            height:100%;
                        }
                    }
                }
            }
        }
        .hotClassify{
            margin-top:40px;/*px*/
            text-align:center;
            & span{
                font-size:28px;/*px*/
            }
            .hotEach{
                margin-top:35px;/*px*/
                display:flex;
                flex-wrap: wrap;
                justify-content: space-around;
                & .recommendClassify{
                    width:25%;
                }
            }
        }
        .guessYouLike{
            margin-top:40px;/*px*/
            text-align:center;
            & span{
                font-size:28px;/*px*/
            }
            .guessLike{
                margin-top:20px;/*px*/
                display:flex;
                flex-wrap: wrap;
            }
        }
    }
</style>



