<template>
    <div class="recommendClassify" @click="goToDetail()">
        <div class="classifyImg">
            <img :src="classify?classify.image:''" alt="">
        </div>
        <div>{{ classify?classify.title:'' }}</div>
    </div>
</template>
<script>
    export default {
        name:"CirClassify",
        data(){
            return {
                detailFcid:null
            }
        },
        props:{
            classify:null
        },
        methods:{
            fetchFcid(){
                var str = this.classify.link;
                if(str.indexOf("fcid")>0){
                    var start = str.indexOf("=");
                    var end = str.indexOf("&");
                    str = str.substring(start+1,end);
                    this.detailFcid = str;
                }
            },
            goToDetail(){

                this.$router.push("/details?detailFcid="+this.detailFcid+"&title="+this.classify.title)
            }
        },
        mounted() {
            this.fetchFcid();
        },
    }
</script>
<style lang="less" scoped>
    .recommendClassify{
        width:20%;
        height:150px;/*px*/
        text-align: center;
        font-size:22px;/*px*/
        font-weight: 300;
        .classifyImg{
            width:120px;/*px*/
            height:120px;/*px*/
            margin:0 auto;
            & img{
                width:100%;
                height:100%;
            }
        }
    }
</style>



