<template>
    <div class="swiperDiv">
        <swiper :options="swiperOptions" ref="mySwiper">
            <swiper-slide v-for="(v,i) of wrapImgs" :key="'wrap'+i">
                <img :src="v.image_800">
            </swiper-slide>
            <div class="swiper-pagination"  slot="pagination"></div>
        </swiper>
  </div>
</template>
<script>
    import { swiper,swiperSlide } from "vue-awesome-swiper";

    export default {
        name:"Swiper",
        data(){
            return {
                swiperOptions:{
                    loop:true,
                    pagination:'.swiper-pagination',
                    autoplay:2000
                }
            }
        },
        props:{
            wrapImgs:null
        },
        components:{
            swiper,
            swiperSlide
        },
        computed: {
            swiper() {
                return this.$refs.mySwiper.swiper
            }
        }
    }
</script>
<style lang="less" scoped>
    .swiperDiv{
        width:100%;
        height:350px;/*px*/
        & img{
            width:100%;
            height:100%;
        }
    }
</style>



