<template>
    <div>
        <SearchBar></SearchBar>
        <div class="homeBody">
            <div class="wrapper" ref="typeWrapper">
                <div class="content">
                    <div :class="typeRightParams.pid==v.maitKey?'typeEach classChange':'typeEach'" v-for="(v,i) of type.leftList" :key="'typeLeft'+i" @click="changeType(v.maitKey,v.miniWallkey)">
                        <div>{{ v.title }}</div>
                    </div>
                </div>
            </div>
            <div class="wrapper" ref="contentWrapper">
                <div class="content">
                    <div class="typeComponent">
                        <CirClassify v-for="(v,i) of typeRight" :key="'typeRight'+i" :classify = "v"></CirClassify>
                        <div class="typeGoodsContainer">
                            <div class="threeType">
                                <div :class="goodsType.sort==v.sortKey?'goodsTypeEach selectedType':'goodsTypeEach'" v-for="(v,i) of typeComtent" :key="'type'+i" @click="changeSort(v.sortKey)">{{ v.title }}</div>
                            </div>
                            <div class="typeGoodsInfo">
                                <TypeGoods v-for="(v,i) of goodsContent" :key="'goodscontent'+i" :likes="v"></TypeGoods>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <Footer></Footer>
    </div>
</template>
<script>
    import BScroll from 'better-scroll';
    import {mapState,mapMutations,mapActions,mapGetters} from "vuex";
    import api from '../server/api';

    export default {
        name:"TypeComponent",
        data(){
            return {
                typeRightParams:{
                    appPlat: "m",
                    pid: "41888"
                },
                goodsType:{
                    fcid:"10062603",
                    cKey:"h5-cube",
                    page:"1",
                    sort:"pop"
                },
                typeRight:[],
                typeComtent:[],
                goodsContent:[],
                fcid:null
            }
        },
        computed:{
            ...mapState(['type'])
        },
        methods:{
            loadType(){
                this.$nextTick(()=>{
                    this.scroll = new BScroll(this.$refs.typeWrapper,{
                        click:true
                    })
                }),
                this.$nextTick(()=>{
                    this.scroll = new BScroll(this.$refs.contentWrapper,{
                        click:true
                    })
                })
            },
            changeType(pid,fcid){
                this.typeRightParams.pid=pid;
                this.goodsType.fcid=fcid;
                this.goodsType.sort="pop";
                this.loadRightType();
                this.loadGoodsInfo();
            },
            changeSort(sort){
                this.goodsType.sort=sort;
                this.loadGoodsInfo();
            },
            loadRightType(){
                api.loadTypeInfo(this.typeRightParams).then(({data})=>{
                    this.typeRight = data.categoryNavigation.list;
                })
            },
            loadGoodsInfo(){
                api.loadDataList(this.goodsType).then(({result})=>{
                    this.typeComtent = result.sortFilter;
                    this.goodsContent = result.wall.docs;
                })
            },
            ...mapActions(["loadTypeData"])
        },
        mounted() {
            this.loadType();
            this.loadTypeData();
            this.loadRightType();
            this.loadGoodsInfo();
        },
    }
</script>
<style lang="less" scoped>
    .homeBody{
        position: absolute;
        top:110px;/*px*/
        left:0;
        right:0;
        bottom:103px;/*px*/
        display:flex;
        &>div:first-of-type{
            width:30%;
            height:100%;
        }
        &>div:last-of-type{
            width:70%;
            height:100%;
        }
        .typeEach{
            width:100%;
            height:100px;/*px*/
            background: #ebebeb;
            position: relative;
            &>div{
                width:80%;
                position: absolute;
                top:50%;
                left:50%;
                text-align:center;
                font-size:30px;/*px*/
                font-weight: bold;
                margin-top:-20px;/*px*/
                margin-left:-110px;/*px*/
            }
        }
        .classChange{
            background-color: white;
            color:#FF5577;
            &>div{
                border-left:10px solid #FF5577;
            }
        }
        .typeComponent{
            display:flex;
            flex-wrap: wrap;
            .recommendClassify{
                width:33%;
                height:200px;/*px*/
                margin-top:20px;/*px*/
                font-size:26px;/*px*/
                line-height:80px;/*px*/
            }
            .typeGoodsContainer{
                width:100%;
                .threeType{
                    height:60px;/*px*/
                    margin-top:60px;/*px*/
                    border-bottom: 1px solid #ebeebe;
                    display:flex;
                    justify-content: space-between;
                    .goodsTypeEach{
                        width:33%;
                        height:30px;/*px*/
                        text-align:center;
                        font-size:26px;/*px*/
                        border-left:1px solid #ccc;
                    }
                    .selectedType{
                        color:#FF5577;
                    }
                }
                .typeGoodsInfo{
                    margin-top:20px;/*px*/
                    display:flex;
                    flex-wrap: wrap;
                }
            }
        }
        
    }
</style>




