<template>
    <div class="searchContent">
        <div class="msg">
            <i class="iconfont icon-xiaoxizhongxin"></i>
        </div>
        <router-link to="/search" tag="div" class="search">
            <div class="searchBar">
                <i class="iconfont icon-sousuo"></i>
                <span>{{ search.sketch }}</span>
            </div>
        </router-link>
        <router-link to="/cart" tag="div" class="cart">
            <i class="iconfont icon-gouwuche"></i>
        </router-link>
    </div>
</template>
<script>
    import {mapState,mapMutations,mapActions,mapGetters} from "vuex";

    export default {
        name:"searchBar",
        computed:{
            ...mapState(['search'])
        },
        methods:{
            ...mapActions(["loadKeyWords"])
        },
        mounted(){
            this.loadKeyWords();
        }
    }
</script>
<style lang="less" scoped>
    .searchContent{
        width:100%;
        height:110px;/*px*/
        border-bottom:1px solid #ccc;/*px*/
        box-sizing: border-box;
        line-height: 110px;
        text-align:center;
        display:flex;
        z-index:999;
        position: relative;
        background-color: white;
        .msg{
            width:14%;
            height:100%;
        }
        .search{
            width:72%;
            height:100%;
            &>.searchBar{
                border:1px solid #ccc;
                width:100%;
                height:60px;/*px*/
                margin:25px auto;/*px*/
                border-radius: 15px;/*px*/
                background-color:#EBEBEB;
                text-align:left;
                line-height:60px;
                color:#CECECE;
                font-size:30px;/*px*/
            }
        }
        .cart{
            width:14%;
            height:100%;
        }
        & i{
            font-size:44px;/*px*/  
        }
        & .icon-sousuo{
            font-size:32px;/*px*/
            display:inline-block;
            margin-left:20px;/*px*/
            
        }
    }
</style>



