<template>
    <div class="goodsContainer">
        <div class="goodsContent">
            <div class="goodsImg">
                <img :src="goodsInfo.img">
            </div>
            <div class="goodsName">
                <div class="goodsTitle">
                    <span>{{ goodsInfo.title }}</span>
                </div>
                <div class="goodsPrice">
                    <div><span>¥</span><span>{{ goodsInfo.price | addZero }}</span></div>
                    <div>
                        <span>¥{{ goodsInfo.orgPrice | addZero }}</span>
                        <span>{{ getDiscount }}</span>
                    </div>
                </div>
                <div class="goodsPost">
                    <span>免邮费</span>
                </div>
            </div>
        </div>
        <div v-if="flag" class="commentContent withoutComment">{{ comment }}</div>
        <div v-else class="commentContent">
            <div>
                <div>
                    <span>买家评价 352 ｜ 销量 {{ goodsInfo.sale }}</span>
                </div>
                <div><span>&gt;</span></div>
            </div>
            <div v-if="rateTags.length!=0" class="commentDesc">
                <span v-for="(v,i) of rateTags" :key="'tags'+i">{{ v.value }}({{ v.num }})</span>
            </div>
            <div class="marginContent">
                <div class="commentDetails" v-for="(v,i) of commentShow" :key="'commentShow'+i">
                    <div>
                        <div class="userImg">
                            <img :src="v.user.avatar">
                        </div>
                        <div class="userName">
                            <span>{{ v.user.uname }}</span>
                        </div>
                    </div>
                    <div class="commentText">
                        <span>{{ v.content }}</span>
                        <br>
                        <span>{{ v.time }}</span>
                        <br>
                        <div class="commentImgContent">
                            <div v-for="(item,index) of v.images" :key="'commentImg'+index" class="eachImg">
                                <img :src="item">
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="addCart" @click="addCart()">
            <span>加入购物车</span>
        </div>
    </div>
</template>
<script>
    import api from '../server/api';

    export default {
        name:"GoodsDetailsInfo",
        data(){
            return {
                goodsInfo:{},
                goodsParams:{
                    type:"1",
                    itemId:"",
                    appPlat:"m"
                },
                commentList:[],
                rateTags:[],
                rateScore:[],
                commentShow:[],
                flag:"false",
                comment:"暂无评论"
            }
        },
        filters:{
            addZero(price){
                if(parseInt(price)==price){
                    return price+".00"
                }
                return price;
            }
        },
        computed:{
            getDiscount(){
                let discount = parseInt(this.goodsInfo.price/this.goodsInfo.orgPrice*10);
                return discount+"折";
            }
        },
        methods:{
            loadGoodsInfo(){
                this.goodsInfo = this.$route.query;
                let str = this.goodsInfo.link;
                if(str.indexOf("itemId")>0){
                    let item = str.substring(str.indexOf("itemId"))
                    let itemId = item.substring(7,item.indexOf("&"))
                    this.goodsParams.itemId = itemId;
                }
            },
            loadGoodsComment(){
                api.loadComment(this.goodsParams).then(({data})=>{
                    this.flag = false;
                    this.commentList = data.list;
                    this.rateTags = data.rateTags;
                    this.rateScore = data.rateScore;
                    this.commentShow = this.commentList.slice(0,2);
                }).catch((error)=>{
                    this.flag = true;
                })
            },
            addCart(){
                let cartInfo = {
                    'tradeItemId':this.goodsInfo.tradeItemId,
                    'goodsTitle':this.goodsInfo.title,
                    'goodsPrice':this.goodsInfo.price,
                    'goodsOrgPrice':this.goodsInfo.orgPrice,
                    'goodsImg':this.goodsInfo.img,
                    'count':1,
                    'flag':false
                }
                if(!JSON.parse(sessionStorage.getItem(('goodsInfo')))){
                    let goodsInfo=[cartInfo]
                    sessionStorage.setItem('goodsInfo',JSON.stringify(goodsInfo));
                }else{
                    let goodsInfo = JSON.parse(sessionStorage.getItem(('goodsInfo')));
                    goodsInfo[goodsInfo.length] = cartInfo;
                    let result = this.checkUnique(goodsInfo);
                    if(result){
                        goodsInfo = JSON.parse(sessionStorage.getItem(('goodsInfo')));
                        for(let i =0;i<goodsInfo.length;i++){
                            if(goodsInfo[i].goodsTitle==cartInfo.goodsTitle){
                                goodsInfo[i].count++;
                            }
                        }
                        sessionStorage.setItem('goodsInfo',JSON.stringify(goodsInfo));
                        return;
                    }
                    sessionStorage.setItem('goodsInfo',JSON.stringify(goodsInfo));
                }      
            },
            checkUnique(ary) {
                let temp = [];
                for(var j=0;j<ary.length;j++){
                    if(temp.indexOf(ary[j].tradeItemId)>=0){
                        return true;
                    }
                    temp.push(ary[j].tradeItemId);
                }
                return false;
            }
        },
        mounted(){
            this.loadGoodsComment();
        },
        created(){
            this.loadGoodsInfo();

        }
    }
</script>
<style lang="less" scoped>
    .goodsContainer{
        background: #ebebeb;
        .goodsContent{
            background:white;
            .goodsImg{
                width:80%;
                height:800px;/*px*/
                margin:0 auto;
                &>img{
                    width:100%;
                    height:100%
                }
            }
            .goodsName{
                margin:0 auto;
                width:96%;
                height:350px;/*px*/
                position:relative;
                .goodsTitle{
                    font-size:36px;/*px*/
                    margin-top:20px;/*px*/
                }
                .goodsPrice{
                    margin-top:10px;/*px*/
                    width:50%;
                    display:flex;
                    position: relative;
                    &>div:first-of-type{
                        display:inline-block;
                        &>span:first-of-type{
                            font-size:32px;/*px*/
                            display:inline-block;
                            position: absolute;
                            top:16px;/*px*/
                            left:0;
                        }
                        &>span:last-of-type{
                            padding:20px;/*px*/
                            font-weight: 500;
                            font-size:72px;/*px*/
                        }
                    }
                    &>div:last-of-type{
                        &>span{
                            display:block;
                            font-size:24px;/*px*/
                        }
                        &>span:first-of-type{
                            margin-top:20px;/*px*/
                            text-decoration: line-through;
                        }
                        &>span:last-of-type{
                            width:80%;
                            padding:0 10px;/*px*/
                            color:#f25;
                            background:#ffe8ee;
                        }
                    }
                }
                .goodsPost{
                    position:absolute;
                    bottom:20px;/*px*/
                    left:10px;/*px*/
                    font-size:26px;/*px*/
                }
            }
        }
        .withoutComment{
            font-size:30px;/*px*/
            text-align:center;
            padding-top:20px;/*px*/
            padding-bottom:150px;/*px*/
        }
        .commentContent{
            margin-top:20px;/*px*/
            background:white;
            &>div:first-of-type{
                width:96%;
                margin:0 auto;
                display:flex;
                justify-content: space-between;
                height:50px;/*px*/
                line-height: 50px;
                color:gray;
                &>div:first-of-type{
                    width:50%;
                }
                &>div:last-of-type{
                    text-align: right;
                    width:10%;
                    font-size:36px;/*px*/
                }
            }
            .commentDesc{
                width:96%;
                margin:0 auto;
                height:150px;/*px*/
                overflow: hidden;
                &>span{
                    display: inline-block;
                    margin:10px;/*px*/
                    padding:10px 10px;/*px*/
                    color:#f25;
                    background:#ffe8ee;
                }
            }
            .marginContent{
                margin-bottom:120px;/*px*/
                .commentDetails{
                    width:96%;
                    margin:20px auto;/*px*/
                    border-bottom:1px solid #ccc;
                    &>div:first-of-type{
                        display:flex;
                        .userImg{
                            width:80px;/*px*/
                            height:80px;/*px*/
                            &>img{
                                width:100%;
                                height:100%;
                                border-radius: 1000px;/*px*/
                            }
                        }
                        .userName{
                            width:50%;
                            height:80px;/*px*/
                            font-size:32px;/*px*/
                            line-height: 80px;/*px*/
                            padding-left:20px;/*px*/
                        }
                    }
                    .commentText{
                        width:100%;
                        margin-top:14px;/*px*/
                        font-size:28px;/*px*/
                        &>span:nth-of-type(2){
                            display:inline-block;
                            margin-top:10px;/*px*/
                            font-size:24px;/*px*/
                        }
                        .commentImgContent{
                            display:flex;
                            flex-wrap:wrap;
                            margin:20px 0px 60px;/*px*/
                            .eachImg{
                                width:20%;
                                height:150px;/*px*/
                                margin-right:10px;/*px*/
                                &>img{
                                    width:100%;
                                    height:100%;
                                }
                            }
                        }
                    }
                }
            }
        }
        .addCart{
            width:100%;
            height:100px;/*px*/
            background:#ffe6e8;
            position:fixed;
            bottom:0;
            left:0;
            color:#ff5777;
            z-index:999;
            font-size:36px;/*px*/
            text-align:center;
            line-height:100px;/*px*/
        }
    }
</style>



