import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '../components/HelloWorld'
import HomePage from '../components/HomePage'
import SearchGoods from '../components/SearchGoods'
import TypeComponent from '../components/TypeComponent'
import CartComponent from '../components/CartComponent'
import TypesDetails from '../components/TypesDetails'
import SearchBarToGoods from '../components/SearchBarToGoods'
import GoodsDetailsInfo from '../components/GoodsDetailsInfo'

Vue.use(Router)

export default new Router({
  mode:"history",
  linkActiveClass:"lactive",
  linkExactActiveClass:"lactive-exact",
  routes: [
    {path:"/",redirect:"/home"},
    {path: '/home',name: 'HomePage',component: HomePage},
    {path: '/hello',name: 'HelloWorld',component: HelloWorld},
    {path:'/search',name: 'SearchGoods',component:SearchGoods},
    {path:'/type',name:'TypeComponent',component:TypeComponent},
    {path:'/details',name:'TypesDetails',component:TypesDetails,props:true}, 
    {path:'/cart',name:'CartComponent',component:CartComponent},
    {path:'/searchToGoods',name:'SearchBarToGoods',component:SearchBarToGoods},
    {path:'/goodsInfo/:likes?',name:'GoodsDetailsInfo',component:GoodsDetailsInfo,props:true}
  ]
})



