import api from '../../server/api';

const state = {
    wrapImgs:[],
    classify:[],
    bebefit:[],
    hotType:[],
    likes:[]
}

const mutations = {
    setWrapImgs(state,data){
        state.wrapImgs = data;
    },
    setClassify(state,data){
        state.classify = data;
    },
    setBebefit(state,data){
        state.bebefit = data;
    },
    setHotType(state,data){
        state.hotType = data;
    },
    setLikes(state,data){
        state.likes = data;
    }
}

const homeParams = {
    appPlat: "m",
    pids: "51822,106930,51833,51836"
}

const listLikes = {
    pid:"9750",
    sort:"pop",
    cKey:46
}


const actions = {
    loadHomeData({ commit }){
        api.loadDatasByPids(homeParams).then(({ data })=>{
            commit("setWrapImgs",data[51822].list);
            commit("setClassify",data[106930].list);
            commit("setBebefit",data[51833].list);
            commit("setHotType",data[51836].list);
        }),
        api.loadDataList(listLikes).then(({result:{wall}})=>{
            commit("setLikes",wall.docs);
        })
    }
}

const getters = {
    
}

export default {
    state,
    actions,
    getters,
    mutations
}




