
import SearchBar from './components/SearchBar'
import Footer from './components/Footer'
import Swiper from './components/Swiper'
import CirClassify from './components/CirClassify';
import GoodsInfo from './components/GoodsInfo';
import TypeGoods from './components/TypeGoods';
import searchToGoods from './components/searchToGoods';

let plugins = {};

plugins.install = function(Vue,options){
    Vue.component("SearchBar",SearchBar);
    Vue.component("Footer",Footer);
    Vue.component("Swiper",Swiper);
    Vue.component("CirClassify",CirClassify);
    Vue.component("GoodsInfo",GoodsInfo);
    Vue.component("TypeGoods",TypeGoods);
    Vue.component("searchToGoods",searchToGoods);
}

export default plugins;


