<template>
    <div class="goodsInfo"  @click="goToGoodsInfo(likes)">
        <div>
            <div class="goodsImg">
                <img :src="likes.img">
            </div>
            <div class="goodsName">
                <span v-for="(v,i) of likes.props" :key="'searchTo'+i">{{ v }}</span>
            </div>
            <div class="goodsPrice">
                <div>{{ "￥"+likes.price }}</div>
                <div>{{ likes.cfav }}<i class="iconfont icon-shoucang"></i></div>
            </div>
        </div>
    </div>
</template>
<script>
    export default {
        name:"searchToGoods",
        props:{
            likes:null
        },
        methods:{
            goToGoodsInfo(likes){
                this.$router.push({path:'/goodsInfo',query:likes});
            }
        }
    }
</script>
<style lang="less" scoped>
    .goodsInfo{
        width:50%;
        height:670px;/*px*/
        &>div{
            width:90%;
            height:100%;
            margin:0 auto;
            .goodsImg{
                width:100%;
                height:500px;/*px*/
                border-radius: 20px;/*px*/
                & img{
                    width:100%;
                    height:100%;
                }
            }
            .goodsName{
                margin-top:20px;/*px*/
                width:100%;
                text-overflow: ellipsis;
                overflow: hidden;
                display: -webkit-box;
                -webkit-line-clamp: 2;
                -webkit-box-orient: vertical;
                word-break: break-all;
                font-size: 24px;/*px*/
                &>span{
                    display:inline-block;
                    margin:3px;/*px*/
                    padding:0px 5px;/*px*/
                    background-color:#ebebeb;
                }
            }
            .goodsPrice{
                height:70px;/*px*/
                display:flex;
                justify-content: space-between;
                line-height: 70px;/*px*/
                & div:first-child{
                    font-size:28px;/*px*/
                    font-weight: 600;
                }
            }
        }
    }
</style>



