import jsonpUtil from "../util/jsonpUtil";

const mce = "https://mce.mogucdn.com/jsonp/";

const list = "https://list.mogujie.com/search";

const typeInfo = "https://mce.mogujie.com/jsonp/makeup/3";

const keyWords = "https://list.mogujie.com/module/mget";

const comment = "https://rate.mogujie.com/h5/rate/jsonp/x6.rate.ratelist/v1";

export default {
    loadDatasByPids(params){
        let url = mce+"multiget/3";
        return jsonpUtil.request(url,params);
    },
    loadDataList(params){
        let url = list;
        return jsonpUtil.request(url,params);
    },
    loadTypeInfo(params){
        let url = typeInfo;
        return jsonpUtil.request(url,params);
    },
    loadKeyWord(params){
        let url = keyWords;
        return jsonpUtil.request(url,params);
    },
    loadComment(params){
        let url = comment;
        return jsonpUtil.request(url,params);
    }
}


