<template>
    <div>
        <div class="search">
            <div class="headerLeft" @click="$router.history.go(-1)"><i class="iconfont icon-xiangzuojiantou"></i></div>
            <div class="searchBar">
                <input type="text" v-model="tempSearch" @focus="changeFlag(1)" @blur="changeFlag(0)" :class="flag?'focusColor':''" :placeholder="search.sketch">
            </div>
            <div class="searchBtn" @click="addSearchHistory(1)">
                <span>搜索</span>
            </div>
        </div>
        <transition enter-active-class="animated bounceInUp" leave-active-class="animated bounceOutDown">
            <div v-show="flag" class="searchBody">
                <div class="searchHistory">
                    <div class="historyTitle">
                        <div>
                            <i class="iconfont icon-sousuo"></i>
                            <span>历史搜索</span>
                        </div>
                        <div>
                            <i class="iconfont icon-lajixiang" @click="addSearchHistory(-1)"></i>
                        </div>
                    </div>
                    <div class="searchList">
                        <div v-if="searchHistory.length==0">暂无历史搜索</div>
                        <span v-else v-for="(v,i) of searchHistory" :key="'searchHistory'+i" @click="getHotWords($event)">{{ v }}</span>
                    </div>
                </div>
                <div class="searchHot">
                    <div>
                        <i class="iconfont icon-shoucang"></i>
                        <span>热门搜索</span>
                    </div>
                    <div class="hotWord">
                        <span v-for="(v,i) of search.hotWord" :key="'hotword'+i" :class="v.color==''?'':'fontClass'" @click="getHotWords($event)">{{ v.query }}</span>
                    </div>
                </div>
            </div>
        </transition>
    </div>
</template>
<script>
import {mapState,mapMutations,mapActions,mapGetters} from "vuex";

export default {
    name: "SearchGoods",
    data(){
        return {
            flag:0,
            tempSearch:"",
            searchHistory:[],
            divShow:false
        }
    },
    computed:{
            ...mapState(['search'])
        },
    methods:{
        changeFlag(e){
            this.flag = e;
        },
        loadSearchHistory(){
            let str = localStorage.getItem('searchHistory');
            if(str){
                this.searchHistory = str.split(",")
            }
        },
        addSearchHistory(num){
            let str = localStorage.getItem('searchHistory');
            if(!str){
                localStorage.setItem('searchHistory','');
                
            }
            if(num==1){
                if(this.tempSearch==""){
                    this.tempSearch = this.search.sketch;
                    if(str==''){
                        str = this.search.sketch;
                    }else{
                        str += ","+this.search.sketch;
                    }
                    localStorage.setItem('searchHistory',str)
                }else{
                    str += ","+this.tempSearch;
                    localStorage.setItem('searchHistory',str)
                }
                this.$router.push("/searchToGoods?keyWord="+this.tempSearch);
                return;
            }
            localStorage.setItem('searchHistory','')
            this.searchHistory = [];
        },
        getHotWords(e){
            this.tempSearch = e.target.innerHTML;
            let str = localStorage.getItem('searchHistory');
            if(!str){
                localStorage.setItem('searchHistory','');
            }
            if(str==''){
                str = this.tempSearch;
            }else{
                str += ","+this.tempSearch;
            }
            localStorage.setItem('searchHistory',str)
            this.$router.push("/searchToGoods?keyWord="+this.tempSearch);
        },
        changedivShow(){
            this.flag = true;
        },
        ...mapActions(["loadKeyWords"])
    },
    mounted(){
        this.loadKeyWords();
        this.changedivShow();
        this.loadSearchHistory();
    },
};
</script>
<style lang="less" scoped>
    .search{
        width:100%;
        height:80px;/*px*/
        display:flex;
        color:#AAAAAA;
        border-bottom:1px solid #ebebeb;
        .headerLeft{
            width:12%;
            height:100%;
            // border:1px solid #ccc;
            position: relative;
            &>i{
                display:inline-block;
                font-size:48px;/*px*/
                position: absolute;
                top:50%;
                left:50%;
                margin-left:-20px;/*px*/
                margin-top:-25px;/*px*/
            }
        }
        .searchBar{
            width:76%;
            height:100%;
            position:relative;
            & input{
                width:90%;
                height:70%;
                border:1px solid  #AAAAAA;
                border-radius: 14px;/*px*/
                position: absolute;
                top:10px;/*px*/
                outline:none;
                padding:0px 20px;/*px*/
                font-size:26px;/*px*/
            }
            .focusColor{
                border:1px solid #FF5577;
            }
        }
        .searchBtn{
            position: relative;
            width:12%;
            line-height: 80px;
            font-size:28px;/*px*/
            color:black;
        }
    }
    .searchHistory{
        width:100%;
        font-size:28px;/*px*/
        color:#AAAAAA;
        border-bottom: 1px solid #ebebeb;
        .historyTitle{
            width:92%;
            margin:0px auto;
            margin-top:36px;/*px*/
            display: flex;
            justify-content: space-between;
            &>div:last-of-type{
                margin-right:20px;/*px*/
                & i{
                    font-size:36px;/*px*/
                }
            }
        }
        .searchList{
            width:82%;
            margin:22px auto;/*px*/
            &>span{
                color:#666;
                margin:10px 10px 0px;/*px*/
                display:inline-block;
                border:1px solid #ccc;
                border-radius: 10px;/*px*/
                padding:5px 10px;/*px*/
            }
        }
    }
    .searchHot{
        margin-top:30px;/*px*/
        width:100%;
        font-size:28px;/*px*/
        color:#AAAAAA;
        &>div:first-of-type{
            width:92%;
            margin:0px auto;
        }
        .hotWord{
            width:80%;
            margin:0 auto;
            &>span{
                color:#666;
                margin:20px 10px 0px;/*px*/
                display:inline-block;
                border:1px solid #ccc;
                border-radius: 10px;/*px*/
                padding:5px 10px;/*px*/
            }
            .fontClass{
                color:#ff4466
            }
        }
    }
</style>



