import api from '../../server/api';

const state = {
    sketch:"",
    hotWord:[],
    searchList:[]
}
const mutations = {
    setSketch(state,data){
        state.sketch = data;
    },
    setHowWord(state,data){
        state.hotWord = data;
    },
    setSearchList(state,data){
        state.searchList = data;
    }
}

const keyWordsParams = {
    platform:"H5",
    code:"hotWord,sketch"
}

const actions = {
    loadKeyWords({commit}){
        api.loadKeyWord(keyWordsParams).then(({ data })=>{
            commit("setSketch",data.sketch.data.query);
            commit("setHowWord",data.hotWord.data);
        })
    }

}

const getters = {
    
}

export default {
    state,
    actions,
    getters,
    mutations
}


