<template>
  <div>
    <div class="header">header</div>
    <div class="body">
      <div class="wrapper" ref="wrapper">
        <div class="content">
          <div class="aa" v-for="i of 30" :key="i">asasasa</div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
  import BScroll from 'better-scroll';
  
  export default {
    name:"HelloWorld",
    methods:{
      loadWrapper(){
        this.$nextTick(()=>{
          this.scroll = new BScroll(this.$refs.wrapper,{})
        })
      }
    },
    mounted() {
      this.loadWrapper();
    },
  }
</script>

<style lang="less" scoped>
  .header{
    width:100%;
    height:80px;/*px*/
    background:white;
  }
  .body{
    position:absolute;
    top:80px;/*px*/
    left:0px;
    right:0px;
    bottom:80px;/*px*/
    &>.wrapper{
      height:100%;
      & .aa{
        height:2rem;
        font-size:30px;/*px*/
      }
    }
  }
  .footer{
    position: fixed;
    bottom:80px;/*px*/
    background:white;
  }
    
  
  
</style>




