import api from '../../server/api';

const state = {
    leftList:[],
    rightList:[]
}

const mutations = {
    setLeftList(state,data){
        state.leftList = data;
    },
    setRightList(state,data){
        state.rightList = data;
    }
}

const typeParams = {
    appPlat: "m",
    pids: "41789"
}


const actions = {
    loadTypeData({ commit }){
        api.loadDatasByPids(typeParams).then(({data})=>{
            commit("setLeftList",data[41789].list)
        })
    }
}

const getters = {

}

export default {
    state,
    actions,
    getters,
    mutations
}


