<template>
    <div>
        <div class="cartHeader">
            <div class="headerLeft" @click="$router.history.go(-1)"><i class="iconfont icon-xiangzuojiantou"></i></div>
            <div class="cartBar">
                <span>购物车</span>
            </div>
            <div class="cartChange">
                <span>编辑</span>
                <i class="iconfont icon-xiaoxizhongxin"></i>
            </div>
        </div>
        <div class="cartBody">
            <div class="wrapper" ref="cartWrapper">
                <div class="content">
                    <div class="cartGoodsInfo" v-for="(v,i) of goodsInfo" :key="'cart'+i">
                        <div :class="v.flag?'selectBtn selectedColor':'selectBtn'"  @click="changeFlag(i)"></div>
                        <div class="cartInfo">
                            <div class="cartInfo-img">
                                <img :src="v.goodsImg">
                            </div>
                            <div class="cartInfo-content">
                                <div class="cartInfo-Name">
                                    {{ v.goodsTitle }}
                                </div>
                                <div class="cartInfo-Price">
                                    <div>
                                        <span>￥{{ v.goodsPrice | addZero }}</span>
                                        <span>￥{{ v.goodsOrgPrice | addZero }}</span>
                                    </div>
                                    <div>
                                        <span>x{{ v.count }}</span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="cartBottom">
            <div :class="flag?'allSelect selectedColor':'allSelect'" @click="changeFlag(-1)"></div>
            <div class="selectTxt"><span>全选 (0) </span><span> ¥ {{ sumPrice | addZero }}</span></div>
            <span class="goToBuy">去结算</span>
        </div>
    </div>    
</template>
<script>
    import BScroll from 'better-scroll';

    export default {
        name:"cartComponent",
        data(){
            return {
                goodsInfo:null,
                flag:false,
                sumPrice:0
            }
        },
        filters:{
            addZero(price){
                if(parseInt(price)==price){
                    return price+".00"
                }
                return price;
            }
        },
        methods:{
            loadCartWrap(){
                this.$nextTick(()=>{
                    this.scroll = new BScroll(this.$refs.cartWrapper,{
                        click:true
                    })
                });
                this.goodsInfo = JSON.parse(sessionStorage.getItem('goodsInfo'));
            },
            changeFlag(num){
                if(num==-1){
                    this.flag = !this.flag;
                    for(let i=0;i<this.goodsInfo.length;i++){
                        this.goodsInfo[i].flag = this.flag;
                    }
                    this.addSumPrice();
                    return;
                }
                this.goodsInfo[num].flag = !this.goodsInfo[num].flag;
                this.checkSelected();
                this.addSumPrice();
            },
            checkSelected(){
                var count = 0;
                if(this.flag){
                    for(let i=0;i<this.goodsInfo.length;i++){
                        if(this.goodsInfo[i].flag){
                            count++;
                        }
                    }
                    if(count<this.goodsInfo.length){
                        this.flag = false;
                    }
                }else{
                    for(let i=0;i<this.goodsInfo.length;i++){
                        if(this.goodsInfo[i].flag){
                            count++;
                        }
                    }
                    if(count===this.goodsInfo.length){
                        this.flag = true;
                    }
                }
            },
            addSumPrice(){
                var sum = 0;
                for(let i=0;i<this.goodsInfo.length;i++){
                    if(this.goodsInfo[i].flag){
                        sum += this.goodsInfo[i].goodsPrice
                    }
                }
                this.sumPrice = sum;
            }
        },
        mounted(){
            this.loadCartWrap();
        }
    }
</script>
<style lang="less" scoped>
    .cartHeader{
        width:100%;
        height:80px;/*px*/
        display:flex;
        top:0;
        left:0;
        border-bottom:1px solid #ccc;
        position: fixed;
        background: #ebebee;
        z-index:999;
        .headerLeft{
            width:12%;
            height:100%;
            position: relative;
            &>i{
                display:inline-block;
                font-size:48px;/*px*/
                position: absolute;
                top:50%;
                left:50%;
                margin-left:-20px;/*px*/
                margin-top:-25px;/*px*/
            }
        }
        .cartBar{
            width:72%;
            height:100%;
            position:relative;
            text-align:center;
            font-size:36px;/*px*/
            line-height:80px;
        }
        .cartChange{
            position: relative;
            width:16%;
            line-height: 80px;
            font-size:28px;/*px*/
            color:black;
            &>i{
                line-height:80px;/*px*/
            }
        }
    }
    .cartBody{
        width:100%;
        height:100%;
        position:absolute;
        top:80px;/*px*/
        left:0px;/*px*/
        bottom:100px;/*px*/
        overflow: hidden;
        display:flex;
        .wrapper{
            // width:100%;
            // height:100%;
            .cartGoodsInfo{
                background:white;
                border-top:1px solid #ccc;
                border-bottom: 1px solid #ccc;
                margin-top:30px;/*px*/
                width:100%;
                height:200px;/*px*/
                display:flex;
                .selectBtn{
                    width:40px;/*px*/
                    height:40px;/*px*/
                    border:1px solid #666;/*px*/
                    border-radius: 1000px;/*px*/
                    margin:60px 40px;/*px*/
                }
                .cartInfo{
                    width:80%;
                    height:100%;
                    display:flex;
                    justify-content: space-between;
                    .cartInfo-img{
                        width:25%;
                        height:98%;
                        margin-top:0.5%;
                        &>img{
                            width:100%;
                            height:100%;
                        }
                    }
                    .cartInfo-content{
                        width:70%;
                        .cartInfo-Name{
                            height:60%;
                            font-size:26px;/*px*/
                            font-weight:400;
                        }
                        .cartInfo-Price{
                            height:40%;
                            display:flex;
                            justify-content: space-between;
                            &>div:first-of-type{
                                &>span{
                                    display:inline-block;
                                }
                                &>span:first-of-type{
                                    font-size:30px;/*px*/
                                    font-weight:400;
                                    line-height: 110px;/*px*/
                                }
                                &>span:last-of-type{
                                    text-decoration: line-through;
                                }
                            }
                            &>div:last-of-type{
                                line-height: 110px;/*px*/
                                width:20%;
                                font-size:28px;/*px*/
                            }
                        }
                    }
                }
            }
        }
    }
    .selectedColor{
        background:#f25;
    }
    .cartBottom{
        position:fixed;
        bottom:0;
        left:0;
        width:100%;
        height:100px;/*px*/
        border-top:1px solid #ccc;
        display:flex;
        line-height: 100px;
        background: #ebebee;
        .allSelect{
            width:40px;/*px*/
            height:40px;/*px*/
            border:1px solid #666;/*px*/
            border-radius: 1000px;/*px*/
            position:absolute;
            top:30px;/*px*/
            left:35px;/*px*/
        }
        
        .selectTxt{
            font-size:28px;/*px*/
            &>span:last-of-type{
                color:red;
            }
            position:absolute;
            left:100px;/*px*/
        }
        .goToBuy{
            display:inline-block;
            font-weight:bold;
            color:white;
            background:#f25;
            position:absolute;
            font-size:28px;/*px*/
            right:40px;/*px*/
            padding:0 60px;/*px*/
        }
    }
</style>



