<template>
    <div class="type-goodsInfo" @click="goToGoodsInfo(likes)">
        <div>
            <div class="type-goodsImg">
                <img :src="likes.img">
            </div>
            <div class="type-goodsName">
                {{ likes.title }}
            </div>
            <div class="type-goodsPrice">
                <div>{{ "￥"+likes.price }}</div>
                <div>{{ likes.cfav }}<i class="iconfont icon-shoucang"></i></div>
            </div>
        </div>
    </div>
</template>
<script>
    export default {
        name:"GoodsInfo",
        props:{
            likes:null
        },
        methods:{
            goToGoodsInfo(likes){
                this.$router.push({path:'/goodsInfo',query:likes});
            }
        }
    }
</script>
<style lang="less">
    .type-goodsInfo{
        width:50%;
        height:500px;/*px*/
        &>div{
            width:94%;
            height:100%;
            margin:0 auto;
            .type-goodsImg{
                width:100%;
                height:350px;/*px*/
                border-radius: 20px;/*px*/
                & img{
                    width:100%;
                    height:100%;
                }
            }
            .type-goodsName{
                margin-top:20px;/*px*/
                width:100%;
                text-overflow: ellipsis;
                overflow: hidden;
                display: -webkit-box;
                -webkit-line-clamp: 1;
                -webkit-box-orient: vertical;
                word-break: break-all;
                font-size: 24px;/*px*/
            }
            .type-goodsPrice{
                height:70px;/*px*/
                display:flex;
                justify-content: space-between;
                line-height: 70px;/*px*/
                & div:first-child{
                    font-size:28px;/*px*/
                    font-weight: 600;
                }
            }
        }
    }
</style>



