<template>
    <div>
        <div class="detailsHeader">
            <div>
                <div class="prevPage" @click="$router.history.go(-1)">
                    <i class="iconfont icon-xiangzuojiantou"></i>
                </div>
                <router-link to="/search" tag="div" class="searchBar">
                    <i class="iconfont icon-sousuo"></i>
                </router-link>
                <router-link to="/cart" tag="div" class="detailCart">
                    <i class="iconfont icon-gouwuche"></i>
                </router-link>
            </div>
            <div class="typeHeader">
                <div :class="params.sort==v.sortKey?'goodsTypeEach selectedType':'goodsTypeEach'" v-for="(v,i) of sort" :key="'type'+i" @click="changeSort(v.sortKey)">{{ v.title }}</div>
                <div class="goodsTypeEach">价格</div>
            </div>
        </div>
        <div class="detailsBody">
            <div class="wrapper" ref="detailWrapper">
                <div class="content detailsContent">
                    <GoodsInfo v-for="(v,i) of detailsContent" :key="'detail'+i" :likes="v"></GoodsInfo>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import api from "../server/api";
    import BScroll from 'better-scroll';

    export default {
        name:"TypesDetails",
        data(){
            return {
                params:{
                    cKey:"16",
                    sort:"pop",
                    page:"1",
                    fcid:"",
                    title:""
                },
                sort:[],
                detailsContent:[]
            }
        },
        methods:{
            loadFcid(){
                this.params.fcid = this.$route.query.detailFcid;
                this.params.title = this.$route.query.title;
            },
            loadDetails(){
                this.$nextTick(()=>{
                    this.scroll = new BScroll(this.$refs.detailWrapper,{
                        click:true
                    })
                })
            },
            loadTypeData(){
                api.loadDataList(this.params).then(({result})=>{
                    this.sort = result.sortFilter;
                    this.detailsContent = result.wall.docs;
                })
            },
            changeSort(sort){
                this.params.sort = sort;
                this.loadTypeData();
            }
        },
        mounted() {
            this.loadFcid();
            this.loadTypeData();
            this.loadDetails();
        },
    }
</script>
<style lang="less" scoped>
    .detailsHeader{
        position: absolute;
        top:0;
        left:0;
        width:100%;
        height:165px;/*px*/
        border:1px solid #ccc;
        display:fixed;
        z-index:999;
        background-color: white;
        &>div:first-of-type{
            border-bottom: 1px solid rgb(177, 175, 175);
            display:flex;
            .prevPage{
                width:6%;
                height:50px;/*px*/
                margin:10px 0px 10px 10px;/*px*/
                text-align:center;
                &>i{
                    font-size:42px;/*px*/
                    line-height:60px;/*px*/
                }
            }
            .searchBar{
                width:79%;
                height:60px;/*px*/
                background-color:#ebebeb;
                margin:10px 20px;/*px*/
                &>i{
                    font-size:32px;/*px*/
                    line-height: 60px;/*px*/
                    padding-left:20px;/*px*/
                    color:#ccc;
                }
            }
            .detailCart{
                height:50px;/*px*/
                margin:10px 0px;/*px*/
                &>i{
                    font-size:42px;/*px*/
                    line-height:50px;
                }
            }
        }
        .typeHeader{
            width:100%;
            height:60px;/*px*/
            margin-top:30px;/*px*/
            display:flex;
            justify-content: space-between;
            .goodsTypeEach{
                width:25%;
                height:30px;/*px*/
                text-align:center;
                font-size:26px;/*px*/
                border-left:1px solid #ccc;
            }
            .selectedType{
                color:#FF5577;
            }
        }
    }
    .detailsBody{
        position:absolute;
        top:170px;/*px*/
        left:0;
        right:0;
        bottom:0;
        display:flex;
        .detailsContent{
            display:flex;
            flex-wrap:wrap;
        }
    }
</style>



